import { Styled } from './EditDomain.styled';
import { Button, Input } from 'antd';
import { Link } from 'react-router-dom';
import PosWord from './PosWord';
import React from 'react';

const { TextArea } = Input;

export default function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that;
  let { dom, synsDict, all_domains } = that.state;

  /*
   * Render:
   */
  return (
    <>
			<Styled className="EditDomain content">
				{/*
				 * FORM (.syns1)
				 */}
        <section className={'domain-section domain-edit'}>
					<h3>Edit ".{dom.key}" synonyms-of-synonyms (words to be extrapolated):</h3>
					<div className={'flexrow posDiv'}>
						<span className={'flexgrow ui-field-input-group'}>
							<Input
                type={'text'}
                placeholder={'comma, separated, words, to add to OK terms...'}
                value={dom.syns1}
                onChange={event => {
                  that.setState({
                    dom: {
                      ...dom,
                      syns1: event.target.value
                    }
                  });
                }}
                onKeyPress={async event => {
                  if (event.key === 'Enter') {
                    that.submit_state_dom();
                  }
                }}
              />
							<Button
                type={'primary'}
                onClick={async () => {
                  that.submit_state_dom();
                }}
              >
								<i className={'fas fa-pen'}></i>
								<b>&nbsp;+&nbsp;</b>
								<i className={'fas fa-save'}></i>
							</Button>
						</span>
					</div>
				</section>

        {/*
				 * RESULTS (synonyms of each word)
				 */}
        {synsDict && Object.keys(synsDict).length > 0 && (
          <section className={'domain-section domain-view'}>
            <div className="cue">user will see "<b>.{dom.key}</b>" extension if they type in any of these words: </div>
						<div className={'domain-syns-paragraph columns'}>
							{Object.entries(synsDict).map((tuple, i) => {
                let key = tuple[0];
                let list = tuple[1];
                return (
                  <div key={key + i} className="column">
										<h3 className="dictKey" style={{ userSelect: "none" }}>
											"
											<Link to={'/edit_word/' + key} target="_blank">
												{key}
											</Link>
											":
										</h3>
										<div>
											{list.map((str, i) => (
                        <div key={str + i} className="dictLine">
													{str},
												</div>
                      ))}
										</div>
									</div>
                );
              })}
						</div>
					</section>
        )}

        {/*
				 * FORM (.syns)
				 */}
        <section className={'domain-section domain-edit'}>
					<h3>Edit ".{dom.key}" direct-synonyms (these words will be used as-is, will NOT be extrapolated):</h3>
					<div className={'posDiv'}>
						<span className={'flexgrow ui-field-input-group'}>
							<TextArea
                placeholder='comma, separated, words, to add to OK terms...'
                rows={50}
                value={dom.syns}
                onChange={event => {
                  that.setState({
                    dom: {
                      ...dom,
                      syns: event.target.value
                    }
                  });
                }}
                onKeyPress={async event => {
                  if (event.key === 'Enter') {
                    that.submit_state_dom();
                  }
                }}
              />
							<Button
                type={'primary'}
                onClick={async () => {
                  that.submit_state_dom();
                }}
              >
								<i className={'fas fa-pen'}></i>
								<b>&nbsp;+&nbsp;</b>
								<i className={'fas fa-save'}></i>
							</Button>
						</span>
					</div>
				</section>

        {/*
				 * All Domains
				 */}
        {all_domains && all_domains.length > 0 && (
          <section className={'domain-section domain-view'}>
						<div>
							<b>all domains: &nbsp;</b>
						</div>
						<p className={'domain-syns-paragraph'}>
							{all_domains.map((d, i) => (
                <PosWord key={d + i} row={d} domext={dom.key}/>
              ))}
						</p>
					</section>
        )}
			</Styled>
		</>
  );
}
