import React from "react";
import { Dropdown, Menu } from "antd";
import { Link } from "react-router-dom";

class PosWord extends React.Component {
  render() {
    if (!this.props.row || !this.props.row.key) {
      return null;
    }
    const menu = (
      <Menu className={"antd-word-definitions-dropdown-menu"}>
				<Menu.Item>
					{(Array.isArray(this.props.row.syns1) ? this.props.row.syns1 : []).join(', ')}
				</Menu.Item>
			</Menu>
    );
    return (
      <span>
				{/*
				 * word
				*/}
        <span className={"posword"}>
					<Dropdown overlay={menu} trigger={['hover']}>
						<Link
              to={`/edit_domain/${this.props.row.key}`}
              onClick={() => {
                window.isLoading('render');
              }}
            >
							{(this.props.domext === this.props.row.key)
                ? <b><u>{this.props.row.key},</u></b>
                : <>{this.props.row.key},</>
              }
						</Link>
					</Dropdown>
				</span>
			</span>
    );
  }
}

export default PosWord;
