import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import * as ui_actions from 'src/redux/actions/ui';
import * as domain_actions from 'src/redux/actions/domain';
import { StyledPage } from '../../Layout.styled';
import DomainsResults from './DomainsResults';
import DomainsHome from './DomainsHome';
import Search from "../../components/Search";
import { throttle } from 'universal-common-scripts/functions/requests';
import { ShowMoreStyled, SearchMoreStyled, SearchTitleStyled, SearchTipStyled } from "./Domains.styled";

class Domains extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tldAdd: '',
      showWip: false,
      showOptions: false,
      gotAvailability: true, // must start out as true!
    };
  }

  componentDidMount() {
  }

  async componentDidUpdate(prevProps) {
    /*
     * Get domain suggestions (when word_input changed, or when tlds list changes)
     */
    if (this.props.word_input) {
      let arr_tlds_unchecked = Object.keys(this.props.tlds_unchecked);
      if (arr_tlds_unchecked.length && this.props.tlds_unchecked !== prevProps.tlds_unchecked) {
        console.log('             RX__domain_suggestions() from Domains.js componentDidUpdate()          bcof tld_list');
        this.props.domain_actions.RX__domain_suggestions();
      }
    }
    /*
     * Get availability (if new suggestions)
     */
    if (this.props.suggestions_domains.length && this.props.suggestions_domains !== prevProps.suggestions_domains) {
      this.setState({
        gotAvailability: false,
      });
      // fetch availability
      await this.props.domain_actions.RX__domain_suggestions_availability();
      this.setState({
        gotAvailability: true,
      });
    }
  }

  toggleResults = (listType) => {
    this.props.domain_actions.RX__results_toggle_list(listType);
  };
  toggleWip = () => {
    this.setState({
      showWip: !this.state.showWip
    });
  };
  toggleOptions = () => {
    this.setState({
      showOptions: !this.state.showOptions
    });
  };
  tld_user = tld => {
    this.props.domain_actions.RX__tld_add(tld, 'user');
  };
  tld_check = tld => {
    this.props.domain_actions.RX__tld_add(tld, 'checked');
  };
  tld_uncheck = tld => {
    this.props.domain_actions.RX__tld_add(tld, 'unchecked');
  };

  render() {
    window.doneLoading('render');

    /*
     * Content
     */
    //
    // Default content
    let PageContent = null;
    let PageSearch = (<Search
      {...this.props}
      className={"Search SearchHome"}
      domains={true}
      title='Domain Name:'
      cue={[
        <span key="1">There are now 1000+ domain extensions!&nbsp;</span>,
        <span key="2">We'll suggest the most relevant ones for you.</span>,
      ]}
    />);
    if (!this.props.word_chunks.length) {
      /*
       * show Homepage
       */
      PageContent = <DomainsHome className="DomainsHome" that={this}/>;
    } else {
      /*
       * show Results
       */
      let cues = undefined;
      if (!this.state.gotAvailability) {
        cues = [<span key="1">Loading domain availability...</span>];
      }
      PageContent = <DomainsResults className="DomainsResults" that={this}/>;
      PageSearch = (<Search
        {...this.props}
        className={"Search"}
        domains={true}
        title={<SearchTitleStyled className="SearchTitle">Domain Name Suggestions</SearchTitleStyled>}
        cue={cues}
      />);
    }

    /*
     * View
     */
    return (
      <>
        {PageSearch}
        <StyledPage>{PageContent}</StyledPage>
      </>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    ui_actions: bindActionCreators(ui_actions, dispatch),
    domain_actions: bindActionCreators(domain_actions, dispatch),
  };
};

const mapStateToProps = function (state) {
  return {
    // word
    word_chunks: state.word.chunks,
    word_input: state.word.input,
    // results
    hide_available: state.domain.hide_available,
    hide_unavailable: state.domain.hide_unavailable,
    hide_other: state.domain.hide_other,
    hide_premium: state.domain.hide_premium,
    // tlds
    tlds_all: state.domain.tlds_all,
    tlds_user: state.domain.tlds_user,
    tlds_checked: state.domain.tlds_checked,
    tlds_unchecked: state.domain.tlds_unchecked,
    // suggestions
    suggestions_availability: state.domain.suggestions_availability,
    suggestions_domains: state.domain.suggestions_domains,
    suggestions_wip_domains: state.domain.suggestions_wip_domains,
    suggestions_phrases: state.domain.suggestions_phrases,
    suggestions_wip_phrases: state.domain.suggestions_wip_phrases,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Domains));
