import React from 'react';
import { DomainsResultsStyled, ShowMoreStyled, ShowWipStyled, ColorsStyled } from './Domains.styled';
import { Button, Checkbox } from 'antd';
import { asort_by_length_asc } from 'universal-common-scripts/functions/asort_strings';
import { Link } from 'react-router-dom';
import MoreOptions from './MoreOptions';
import Tlds from './Tlds';
import { Styled } from './MoreOptions.styled';

const DomainsResults = function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that;
  /*
   * View variables:
   */
  let { suggestions_domains, suggestions_wip_phrases, suggestions_phrases, suggestions_wip_domains, suggestions_availability } = that.props;
  /*
   * Validate
   */
  if (!suggestions_domains || !suggestions_domains.length) {
    return (
      <DomainsResultsStyled>
        <div className="content">
          <p>No results...</p>
        </div>
      </DomainsResultsStyled>
    );
  }

  return (
    <>
      {/*
       * Options
       */}
      <MoreOptions that={that}/>

      {/*
       * BODY / CONTENT / RESULTS
       */}
      <DomainsResultsStyled>
        <div className="content">
          {/*
           * Show more...
           */}
          <ShowWipStyled className="ShowWipStyled" onClick={that.toggleWip} style={{ marginTop: '-0.45rem' }}>
            <span>show the work &thinsp;</span>
            <i className="fas fa-ellipsis-h"></i>
          </ShowWipStyled>

          {/*
           * Columns
           */}
          <ColorsStyled
            className={
              (!that.state.gotAvailability ? ' gettingAvailability' : ' gotAvailability') +
              (that.props.hide_available ? ' hideAvailable' : '') +
              (that.props.hide_unavailable ? ' hideUnavailable' : '') +
              (that.props.hide_other ? ' hideOther' : '') +
              (that.props.hide_premium ? ' hidePremium' : '')
            }
          >
            {/*
             * WIP
             */}
            {!!that.state.showWip && (
              <div className="wip doms columns">
                {/*
                 * DOMAIN SUGGESTIONS
                 */}
                {Object.keys(suggestions_wip_domains || {}).map((title, i) => {
                  let list = suggestions_wip_domains[title];
                  return (
                    <div key={title + i} className="column">
                      <h3>
                        <b>{title}</b>
                      </h3>
                      <div>
                        {/* arr_sort_by_width(item.list)*/}
                        {list.map((phraseString, i) => {
                          return (
                            <div
                              key={i}
                              className={
                                'domname status' + suggestions_availability[phraseString.replace(/ /g, '')] || 0
                              }
                            >
                              {phraseString}
                            </div>
                          );
                        })}
                      </div>
                    </div>
                  );
                })}

                {/*
                 * WIP PHRASES (debugging, temporary)
                 */}
                <div className="column">
                  <div className="nowrap">
                    <b>{'wip phrases'}</b>
                  </div>
                  <div className="pre">
                        {suggestions_phrases.map((phrase, pi) => (
                          <div key={pi}>&quot;{phrase.join(' ')}&quot;</div>
                        ))}
                  </div>
                </div>

                {/*
                 * WIP PHRASE LISTS (debugging, temporary)
                 */}
                {Object.keys(suggestions_wip_phrases || {}).map((title, i) => {
                  let lists = suggestions_wip_phrases[title];
                  return (
                    <div key={title + i} className="column">
                      <div className="nowrap">
                        <b>{title}</b>
                      </div>
                      <div>
                        {lists.map((phrase, pi) => (
                          <div key={pi}>&quot;{phrase.join(' ')}&quot;</div>
                        ))}
                      </div>
                    </div>
                  );
                })}
              </div>
            )}

            {/*
             * Results:
             */}
            <div className="doms">
              {suggestions_domains.map((phraseString, i) => (
                <div
                  key={i}
                  className={'domname status' + (suggestions_availability[phraseString.replace(/ /g, '')] || 0)}
                >
                  {/*<i className="far fa-heart domname-icon"></i>*/}
                  <span>
                    {phraseString.split(' ').map((str, i) => (
                      <span key={str + i} className="word">
                        {str}
                      </span>
                    ))}
                  </span>
                  &nbsp;&nbsp;
                  <span className="domname-v">v</span>
                </div>
              ))}
            </div>

            {/*
             * TLDs
             */}
            {!that.state.showWip && (
              <Tlds that={that}/>
            )}
          </ColorsStyled>
        </div>
      </DomainsResultsStyled>
    </>
  );
};

export default DomainsResults;
