// import all_tlds from "universal-wordsalad-scripts/data/domains/object.js";
import { data_domains_availability } from 'src/redux/actions/api/data_domain';
import { domain_suggestions } from "./api/v1";

/*
 * results
 */
export function RX__results_toggle_list(listType = '') {
  if (listType) {
    return {
      type: 'RX__results_toggle_list',
      listType: listType
    };
  }
}

/*
 * ADD TLD (or remove tld, by "adding" it to "unchecked" list)
 * This is called on page load! When Search.js parses the URL.
 */
export function RX__tld_add(string = '', list = 'unchecked') {
  return async (dispatch, getState) => {
    let action = list;
    if (action.substr(-2) === 'ed') {
      action = action.substring(0, action.length - 2);
    }
    dispatch({
      type: 'RX__tld_' + action,
      tld: string
    });
  };
}

/*
 * suggestions
 */
export function RX__domain_suggestions() {
  return async (dispatch, getState) => {
    // input
    let { word, domain } = getState();
    if (!word.input) {
      return
    }

    // API options
    let options = {
      phrase: word.input,

      domains: true,
      tlds: true,
      phrases: true,
      wip_phrases: true,
      wip_domains: true,

      tlds_use: Object.keys(domain.tlds_user).toString(),
      tlds_ignore: Object.keys(domain.tlds_unchecked).toString(),
    };

    // API request
    let data = await dispatch(domain_suggestions(options));

    // API response
    if (data) {
      dispatch({
        type: 'RX__domain_suggestions',
        results: data
      });
      dispatch({
        type: 'RX__tlds_checked',
        tlds: data.tlds
      });
    }
  };
}

export function RX__domain_suggestions_availability() {
  return async (dispatch, getState) => {
    let { domain } = getState();
    let { suggestions_availability, suggestions_domains } = domain;
    /*
     * Suggestions
     */
    let suggestions = suggestions_domains || [];
    // remove spaces
    suggestions = suggestions.map(word => word.replace(/ /g, ''));
    // keep only ones which don't already have availability score
    suggestions = suggestions.filter(word => {
      return !suggestions_availability[word] || suggestions_availability[word] === "0";
    });
    /*
     * Request availability of suggestions
     */
    return dispatch(data_domains_availability(suggestions))
      .then((response) => {
        if (response && typeof response.data === 'object') {
          // save
          dispatch({
            type: 'RX__domain_suggestions_availability',
            data: response.data
          });
        }
      });
  };
}