import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import { WordPossStyled } from './WordPoss.styled';
import PosWord from 'src/components/EditWord/PosWord.js';
import api_actions from "../../redux/actions/api";

class WordPoss extends React.Component {
  render() {
    let { word_chunks, show_poss } = this.props;
    if (!word_chunks.length) {
      return null;
    }
    let chunks = word_chunks.flat();
    if (!chunks.length) {
      return null;
    }
    /*
     * Render:
     */
    return (
      <WordPossStyled className={'WordPossStyled ' + (show_poss ? ' show' : ' hide')}>
				<section className={'columns'}>
					{chunks.map(row => {
            if (!row || !row.poss) {
              return null;
            }
            let poss = row.poss;
            let poss_keys = Object.keys(poss);
            return (
              <div key={row.key} className="column">
								<b>
									<Link to={`/edit_word/${row.key}`} target="_blank">{row.key}</Link>
								</b>:
								<div className="">
                {poss_keys.map(poss_key => {
                  return (
                    <section key={poss_key} className={'columns'}>
											<div className="column">
                        <h5 className="color-dark nn">{poss_key}:</h5>
												<div className="words">
                        {(poss[poss_key] && poss[poss_key].map)
                          ? (
                            poss[poss_key].map(w => (
                              !poss_key.includes('domains')
                                ? (
                                  <span key={w}>
                                    <PosWord
                                      row={row}
                                      word={w}
                                      info={undefined}
                                      api_actions={this.props.api_actions}
                                    />
                                  </span>
                                )
                                : (
                                  <span key={w}>
                                    <Link target="_blank" to={"/edit_domain/." + w}>{w}</Link>, &thinsp;
                                  </span>
                                )
                            ))
                          )
                          : (
                            <span className="object">{JSON.stringify(poss[poss_key], null, ' ')}</span>
                          )
                        }
                        </div>
											</div>
										</section>
                  );
                })}
                </div>
							</div>
            );
          })}
				</section>
			</WordPossStyled>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    api_actions: bindActionCreators(api_actions, dispatch),
  };
};


const mapStateToProps = function (state) {
  return {
    show_poss: state.ui.show_poss,
    word_chunks: state.word.chunks
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(WordPoss));
