/*
  1) CONFIG - DEFAULT STATE:
*/
const defaultState = {
  input: '', // simple string, user-entered, will be parsed into chunks/tld
  chunks: [], // array of word DB rows: { key: '', poss: {} }
  tld: undefined, // word DB row: { key: '', poss: {} }
};

/*
  2) REDUCE STATE:
*/
const ThisReducer = (state = defaultState, action) => {
  const newState = { ...state };

  /*
    3) HANDLE ACTIONS:
  */
  switch (action.type) {
    /*
     * Input (query)
     */
    case 'RX__word_input':
      newState.input = action.data || defaultState.input;
      break;

    case 'RX__word_chunks':
      newState.chunks = action.data || defaultState.chunks;
      console.log('newState.chunks', newState.chunks);
      break;

    case 'RX__word_tld':
      newState.tld = action.data || defaultState.tld;
      console.log('newState.tld', newState.tld);
      break;
  }
  /*
    4) RETURN (COPY OF) STATE:
  */
  return newState;
};

export default ThisReducer;
