import { domain_suggestions } from 'src/redux/actions/api/v1';

/*
 * words
 */
export function RX__word_chunks(qstring = '') {
  return async dispatch => {

    // API options
    let options = {
      phrase: qstring,
      phrase_chunks: true,
      phrase_tld: true,
      domains: true,
      tlds: true,
      phrases: true,
      wip_phrases: true,
      wip_domains: true,
    };

    // API request
    let data = await dispatch(domain_suggestions(options));

    // API response
    if (data) {
      // read data
      let { phrase, phrase_chunks, phrase_tld } = data;
      // write data
      dispatch({
        type: 'RX__word_input',
        data: phrase
      });
      dispatch({
        type: 'RX__word_chunks',
        data: phrase_chunks
      });
      dispatch({
        type: 'RX__word_tld',
        data: phrase_tld
      });
      dispatch({
        type: 'RX__domain_suggestions',
        results: data
      });
      dispatch({
        type: 'RX__tlds_checked',
        tlds: data.tlds
      });
    }
  };
}