import React from 'react';
import { TldsStyled } from './Tlds.styled';
import { Button, Checkbox } from 'antd';
import { asort_by_length_asc } from 'universal-common-scripts/functions/asort_strings';
import { Link } from 'react-router-dom';
import Search from '../../components/Search';
import { obj_is_empty } from 'universal-common-scripts/functions/objects.js';
import { FontAwesomeIcon as FA } from '@fortawesome/react-fontawesome';
import { faAngleDown as faCaretDown } from '@fortawesome/pro-solid-svg-icons';
import { faTimes, faAngleDown } from '@fortawesome/pro-regular-svg-icons';

const DomainsResults = function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that;
  /*
   * Same as this.render():
   */
  let {
    tlds_user = {},
    tlds_checked = {},
    tlds_unchecked = {}
  } = that.props;
  let tlds_user_arr = Object.keys(tlds_user);
  let tlds_checked_arr = Object.keys(tlds_checked);
  let tlds_unchecked_arr = Object.keys(tlds_unchecked);

  return (
    <TldsStyled className="TldsStyled">

      {/*
       * User extensions
       */}
      <div className="section">
        <div className={'heading'}>
          <div className="title user">
            User choice extension:
          </div>
        </div>
        {!!(tlds_user_arr && tlds_user_arr.length) && (
          <>
            {tlds_user_arr.map((tld, i) => (
              <div key={tld + 'ch' + i} className={'tld select'}>
                <span
                  className="clickable"
                  onClick={() => {
                    that.props.ui_actions.RX__focusSelectTld();
                  }}
                >
                  <b>.</b>{tld}
                </span>
                <FA
                  className="clickable fa-angle-down"
                  icon={faAngleDown}
                  onClick={() => {
                    that.props.ui_actions.RX__focusSelectTld();
                  }}
                />
                {(tlds_user_arr.length !== 1) &&
                <FA
                  icon={faTimes}
                  className="fa-times"
                  onClick={() => {
                    that.tld_uncheck(tld);
                  }}
                />
                }
              </div>
            ))}
          </>
        )}
      </div>

      {/*
       * Checked
       */}
      {!!(tlds_checked_arr && tlds_checked_arr.length) && (
        <div className="section">
          <div className={'heading'}>
            <div className="title checked">
              Top <span className="topN">10<FA className="fa-caret-down" icon={faCaretDown}/></span> suggestions:
            </div>
          </div>
          {tlds_checked_arr.map((tld, i) => (
            <div key={tld + 'ch' + i} className={'tld checked'}>
              <span>
                <b>.</b>
                {tld}
              </span>
              {!(i === 0 && obj_is_empty(tlds_user_arr)) &&
              <FA
                icon={faTimes}
                className="fa-times"
                onClick={() => {
                  that.tld_uncheck(tld);
                }}
              />
              }
            </div>
          ))}
        </div>
      )}
    </TldsStyled>
  );
};

export default DomainsResults;
